import React, {Component} from 'react';
import './footer.scss'
import scan from './images/logo.png';


class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <div className="footer_wrapper">
                <div className="about">
                    <p className="title"></p>
                    <p className="desc">
                        -
                    </p>
                    <p className="desc">
                        2013201710w+H5
                    </p>
                </div>
                <div className="linkUs">
                    <img src={scan} width={'100%'} alt=""/>
                </div>
            </div>
        )
    }


}

export default Index;



