import React, {Component} from 'react';
import {Menu} from 'antd';
import {Link} from 'react-router-dom';

const {SubMenu} = Menu;

const NavArray = [{
    key: 'h5',
    name: 'H5',
    url: '/h5'
}, {
    key: 'minProgram',
    name: '/',
    url: '/minProgram'
}, {
    key: 'minGame',
    name: '',
    url: '/minGame'
}, {
    key: 'custom',
    name: '',
    url: '#custom'
}, {
    key: 'about',
    name: '',
    url: '#about'
}, {
    key: 'link',
    name: '',
    url: '#link'
}];

const other = {
    key: 'language',
    name: 'English',
    children: [{
        key: 'en',
        name: 'English'
    }, {
        key: 'zh',
        name: ''
    }]
};

class Nav extends Component {
    state = {
        current: 'home',
        currentLanguage: {
            key: 'en',
            name: 'English'
        }
    };

    handleClick = (e) => {
        console.log('click ', e);
        this.setState({
            current: e.key,
        });
    };


    render() {
        // const {NavArray} = this.state;
        const list = NavArray.map((elem) => {
            return (
                <Menu.Item key={elem.key}>
                    <Link to={elem.url}>
                        <span>{elem.name}</span>
                    </Link>
                </Menu.Item>
            );
        });
        return (

            <Menu onClick={this.handleClick}
                  theme={'dark'}
                  selectedKeys={[this.state.current]}
                  style={{lineHeight: '62px', background: 'rgb(14, 14, 14)', float: 'right', borderBottom: 'none'}}
                  mode="horizontal">
                {list}
            </Menu>

        );
    };
}

export default Nav;



