import React, {Component} from 'react';
import {Row, Col, Icon} from 'antd';
import './conent.scss';

const list = [
    {id: 1, name: '', icon: 'thunderbolt', color: '#db672e', desc: ''},
    {id: 2, name: '', icon: 'rocket', color: '#1DA57A', desc: ''},
    {id: 3, name: '', icon: 'cloud', color: '#1890ff', desc: 'H5'},
    {id: 4, name: '7X24', icon: 'phone', color: '#eb2f96', desc: ''}
];

class Feature extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        const featureList = list.map((elem, index) => {
            return (
                <Col span={4} key={index}>
                    <div className="item">
                        <div className="icon">
                            <Icon type={elem.icon} theme="twoTone" twoToneColor={elem.color}/>
                        </div>
                        <p className="name">{elem.name}</p>

                        <p className="desc">
                            {elem.desc}
                        </p>
                    </div>
                </Col>)
        });
        return (
            <div className="feature_wrapper pager" id={'feature'}>
                <div className="title">
                    <div className="h2">Why we are</div>
                    <div className={'h3'}></div>
                </div>
                <Row type="flex" justify="space-between" align="middle">

                    {featureList}
                </Row>
            </div>
        )
    }


}

export default Feature;



